<template>
    <div>
        <div>
            <h2>上传图片方式1</h2>
            <!-- // 这种可以打开相机或文件，"jpg,png,gif"这种打开只能上传特定文件且没有相机 -->
            <input type="file" accept="image/*" @change="changeImage()" ref="avatarInput" style="display:none">
            <!-- 隐藏原生上传控件，使用自己上传的图标，并添加element-ui的图片预览功能 -->
            <div class="pic_list_box">
                <div class="pic_list" v-show="imgDatas.length">
                    <div v-for="(src,index) in imgDatas" :key="index">
                        <!-- 利用element-ui的图片预览插件 -->
                        <el-image style="width: 100px; height: 100px" :src="src" :preview-src-list="imgDatas">
                        </el-image>
                    </div>
                </div>
                <!-- 替换自己的上传图标 -->
                <img class="upload_btn" @click="upLoad" src="../assets/img/Icon/out.png" alt />
            </div>
        </div>
        <div>
            <h2>加载模型并预测</h2>
            <el-upload action="" :on-change="handleelchange" :auto-upload="false" list-type="picture-card">
                <i class="el-icon-plus"></i>
            </el-upload>
        </div>
        <div>
            <h2>预测结果显示</h2>
            <img class="upload_btn" @click="upLoad" src="../assets/img/Icon/out.png" alt />
            <a href="../assets/img/Icon/out.png" download="out.png">下载图片</a>

            <img src="https://ssyerv1.oss-cn-hangzhou.aliyuncs.com/picture/337f923e0cbd4b7b9a328135dec89c62.jpg?x-oss-process=image/resize,w_400"
                alt="" />
            <a download="test.jpg"
                href="https://ssyerv1.oss-cn-hangzhou.aliyuncs.com/picture/337f923e0cbd4b7b9a328135dec89c62.jpg?x-oss-process=image/resize,w_400">
                下载图片2
            </a>
 <figure>
    <figcaption>本地图片:</figcaption>
    <img src="../assets/logo.png" alt="" />
    <a href="../assets/logo.png" download="logo.png">下载图片3</a>
    <button id="btn1">下载图片</button>
  </figure>
        </div>
    </div>
</template>

<script>
    import 'element-ui/lib/theme-chalk/index.css';
    const axios = require('axios');
    export default {
        name: "Demo5",
        data() {
            return {
                imgDatas: []
            }
        },
        methods: {
            handleelchange(file, fileList) {
                console.log(file, fileList)
                let formdata = new FormData()
                fileList.map(item => { //fileList本来就是数组，就不用转为真数组了
                    formdata.append("file", item.raw) //将每一个文件图片都加进formdata
                })
                //    axios.post("接口地址", formdata).then(res => { console.log(res) })
                axios.post("1ocalhost:8081", formdata).then(res => {
                    console.log(res)
                })
            },


            changeImage() {
                // 上传图片事件
                var files = this.$refs.avatarInput.files;
                console.log("files:", files)
                console.log("files[0]:", files[0])
                console.log("imgDatas:", this.imgDatas)
                var that = this;

                function readAndPreview(file) {
                    //Make sure `file.name` matches our extensions criteria
                    if (/\.(jpe?g|png|gif)$/i.test(file.name)) {
                        var reader = new FileReader();
                        reader.onload = function (e) {
                            // 防止重复上传
                            if (that.imgDatas.indexOf(e.target.result) === -1) {
                                that.imgDatas.push(e.target.result);
                            }
                        };
                        reader.readAsDataURL(file);
                    }
                }
                readAndPreview(files[0])
                if (files.length === 0) {
                    return;
                }

                // 文件上传服务器
                // this.setUploadFile(files[0])

            },
            setUploadFile(file) {
                this.formData = new FormData()
                this.formData.append('files', file, file.name) // 添加到请求体
                axios.post('/api/dxbase/upload?resType=EVENT', this.formData).then(res => {
                    console.log(res);
                })
                // this.$http
                //     .post('/api/dxbase/upload?resType=EVENT', this.formData)
                //     .then(res => {
                //         console.log(res);
                //     })
            },
            upLoad() {
                // 触发上传图片按钮
                this.$refs.avatarInput.dispatchEvent(new MouseEvent("click"));
            }
        }
    };
</script>

<style scoped>
    .pic_list_box {
        display: flex;
    }

    .upload_btn {
        width: 100px;
        height: 100px;
        padding-left: 15px;
    }

    .pic_list {
        display: flex;
    }
</style>